import "./styles.css";
import React, { Component } from "react";

class Clock extends React.Component {
  formatName(user) {
    return user.firstName + " " + user.lastName;
  }
  state = {
    user: {
      firstName: "Test",
      lastName: "Boy"
    },
    styles: {
      background: "#83b2da"
    }
  };

  user = {
    firstName: "Test",
    lastName: ""
  };

  cd = () => {
    console.log("work");
    this.setState({ styles: { background: "#66a0d1" } });
  };

  render() {
    return (
      <div className="App">
        <div class="element" style={this.state.styles} onClick={this.cd}>
          Hello {this.formatName(this.state.user)}
        </div>
      </div>
    );
  }
}

export default Clock;
